
.listCheck {
  list-style: none;
  padding: 0;
  display: flex;
  gap: 5px;
  margin: 30px 0 0;
}

.itemCheck {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgb(146, 146, 146);
}

.itemCheckTrue {
  background-color: green;
}

.itemCheckFalse {
  background-color: red;
}
  